.logo {
  border-radius: 100%;
  animation: pulsing 4s infinite;
}

@keyframes pulsing {
  0% {
    transform: scale(.5);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(.5);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.hero-pic-box__pictures img {
  animation: spin 5s linear infinite;
}

.hero-pic-box__arrow-icon {
  animation-name: slidein;
  animation-duration: 3s;
}

.hero-pic-box__arrow-icon:hover {
  transform: scale(1.15);
}

@keyframes slidein {
  from {
    width: 300%;
    margin-left: 100%;
  }

  to {
    width: 100%;
    margin-left: 0%;
  }
}

.hero-pic-box__desc {
  animation-name: slidein;
  animation-duration: 3s;
}

.hero-pic-aside__ice-cream {
  animation-name: ice-cream;
  animation-duration: 4s;
  animation-timing-function: cubic-bezier(.1, -.6, .2, 0);
}

@keyframes ice-cream {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

